* {
    box-sizing: border-box;
  }

  body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
      'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: #fafafa;
    color: #212121;
    line-height: 1.5;
  }

  #search-box {
    font: inherit;
    padding: 8px;
    max-width: 320px;
    margin-bottom: 16px;
    margin-left: 30px;
    border: 2px solid #3d8ae0;
    border-radius: 4px;
    box-shadow: 2px 3px 5px 1px rgb(146, 153, 153);
  }
  .search-title {
    margin-left: 30px;
    color: rgb(11, 158, 114);
  }

  .country-img {
    display: block;
    width: 50px;
    height: 30px;
    margin-right: 10px;
  }

  .country-card {
    margin-left: 30px;
  }

  .country-thumb {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
  }

  h3,
  h2 {
    margin-top: 0;
    margin-bottom: 0;
  }

  h3 {
    font-weight: 500;
  }

  .country-text {
    font-weight: bold;
  }